.filter {
    margin-bottom: 30px;
    padding: 30px;
    position: relative;
    box-shadow: 0 4px 24px rgba(0, 0, 0, 0.08);
    background-color: #ffffff;
    &__close {
        display: none;
        position: absolute;
        top: 24px;
        right: 30px;
        width: 40px;
        height: 40px;
        border: none;
        background-color: transparent;
        cursor: pointer;
        transition: all .35s;
    }
    .line {
        position: absolute;
        right: 0;
        width: 40px;
        height: 3px;
        background-color: #ef0b1a;
    }
    .two {
        transform: rotate(90deg);
    }
    .rotate {
        transform: rotate(45deg);
    }
    &-wrap {
        display: flex;
        flex-direction: column;
    }
    &__title {
        margin-bottom:20px;
        padding-bottom: 10px;
        position: relative;
        height: fit-content;
        @include bold;
        font-size: 24px;
        letter-spacing: 0.24px;
        line-height: 34.66px;
        &:before {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 53px;
            height: 2px;
            background-color: #ef0b1a;
        }
    }
    &-town, &-car-body {
        margin-bottom: 10px;
        select {
            margin-top: 15px;
            padding: 9px 30px 9px 35px;
            position: relative;
            width: 100%;
            border: 1px solid #dddddd;
            border-radius: 5px;
            line-height: 25.99px;
            color: rgba(0, 0, 0, 0.3);
            &:before {
                content: '';
                position: absolute;
                right: 0;
                top: 0;
                width: 8px;
                height: 6px;
                background: url('../img/icon--arrow-down.png');
            }
            &:focus {
                color: #000;
            }
        }
    }
    &-mileage, &-volume {
        margin-bottom: 30px;
        &__title {
            margin-bottom: 18px;
        }
        &-range {
            // display: flex;
            // flex-direction: column;
            // align-items: center;
            // justify-content: space-between;
            // span {
            //     margin-right: 10px;
            //     &:nth-child(3) {
            //         margin-left: 20px;
            //     }
            // }
            span {
                margin-left: 20px;
                &:first-child {
                    margin-left: 0;
                }
            }
            
            input[type=text] {
                margin-left: 10px;
                padding: 5px 10px;
                max-width: 70px;
                border: 1px solid #dddddd;
                border-radius: 5px;
                text-align: center;
                color: rgba(0, 0, 0, 0.3);
                &:focus {
                    border-color: #ef0b1a;
                    color: rgba(0, 0, 0, 1);
                }

            }
            #mileage-range, #volume-range {
                width: 250px;
                border: none;
                background-color: #E9E9E9;
            }
            .ui-slider-horizontal {
                height: 5px;
            }
            .ui-widget-header {
                background-color: #ef0b1a;
            }
            .ui-state-default {
                border: 4px solid #F02330;
                border-radius: 50%;
                box-sizing: border-box;
                background: #FFFFFF;
            }
            .ui-slider .ui-slider-handle {
                top: -7px;
                width: 20px;
                height: 20px;
            }
        }
    }
    &-fuel, &-box, &-drive, &-customs, &-condition {
        margin-bottom: 25px;
        &:last-child {
            margin-bottom: 0;
        }
        &-list {
            display: flex;
            flex-direction: column;
            margin-top: 15px;
            label {
                margin-bottom: 15x;
                input {
                    display: none;
                }
                input:checked ~ span:after {
                    transform: scale(1);
                }
                span {
                    display: flex;
                    align-items: center;
                    margin-bottom: 15px;
                    padding-left: 30px;
                    position: relative;
                    color: rgba(0, 0, 0, 0.56);
                    &:before {
                        content: '';
                        position: absolute;
                        top: 0px;
                        left: 0;
                        width: 18px;
                        height: 18px;
                        border: 1px solid #dddddd;
                        border-radius: 2px;
                    }
                    &:after {
                        content: '';    
                        position: absolute; 
                        top: 1px; 
                        left: 1px;
                        width: 17px;
                        height: 17px;
                        background: #ef0b1a;
                        clip-path: polygon(45% 64%, 84% 14%, 100% 28%, 47% 100%, 0 49%, 15% 32%);
                        transform: scale(0);
                        transition: transform .3s;
                    }
                    img {
                        margin-right: 20px;
                    }
                }
            }
        }
    }
}

@media (max-width: 1360px) {
    .filter-wrap * > div{
        margin-right: 20px;
        // margin-bottom: 0;
    }
    .filter-wrap {
        display: flex;
        flex-wrap: wrap;
        flex-direction: row;
        justify-content: flex-start;
    }
    .filter {
        &__header, &__range, &__body, &__footer {
            display: flex;
            flex-wrap: wrap;
            margin-bottom: 15px;
        }
    }
    .filter__title {
        width: 100%;
    }
    .filter-mileage-range, .filter-volume-range {
        .col-filter {
            flex-direction: column;
            span {
                margin: 0;
                margin-bottom: 10px;
            }
            input {
                margin-bottom: 15px;
            }
        }
    }
}

@media (max-width: 768px) {
    .filter {
        border-top: 3px solid #ef0b1a;
        &__close {
            display: block;
        }
    }
    .filter-wrap {
        display: none;
    }
    .db {
        display: flex;
    }
    .filter-mileage, .filter-volume {
        margin-bottom: 30px !important;
        // width: 48%;
    }
    .filter-mileage-range .col-filter, .filter-volume-range .col-filter {
        flex-direction: row;
        span {
            margin-bottom: 0;
        }
        input {
            margin-bottom: 0;
        }
    }
}

@media (max-width: 596px) {
    .filter-mileage, .filter-volume {
        width: 100%;
    }
    .filter-mileage-range .col, .filter-volume-range .col {
        margin-bottom: 5;
        span {
            margin-bottom: 0;
        }
        input {
            margin-bottom: 0;
        }
    }
    .filter-wrap * ~ div{
        margin-bottom: 10px;
    }
}

@media (max-width: 360px) {
    .filter {
        padding: 15px;
    }
    .filter__close {
        top: 16px;
        right: 23px;
    }
}