// Шрифты
//-----------------------------------//
@mixin regular {
    font-family: 'Montserrat', sans-serif;
    font-weight: 400;
}
@mixin medium {
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
}
@mixin bold {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
}
@mixin black {
    font-family: 'Montserrat', sans-serif;
    font-weight: 900;
}