.delivered-title {
    margin: 30px 0;
    &-wrap {
        margin: 0 auto;
        max-width: 1000px;
    }
    h1 {
        padding: 0 0 30px;
        position: relative;
        @include black;
        text-align: center;
        text-transform: uppercase;
        &:before {
            content: '';
            display: block;
            margin: 0 auto;
            position: absolute;
            bottom: 0;
            left: 0;
            right: 0;
            width: 120px;
            height: 6px;
            border-radius: 50px;
            background-color: #ef0b1a;
        }
    }
}

.shortlist {
    &-wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
    }
    &-item {
        margin: 10px;
        // margin-bottom: 30px;
        width: calc(100% / 3 - 20px);
        border: 1px solid #e4e4e4;
        border-radius: 10px;
        .column {
            position: relative;
            overflow: hidden;
            &:hover .details {
                transform: scale(1);
                visibility: visible;
            }
            .details {
                display: flex;
                padding: 10px 0;
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                height: 100%;
                background-color: rgba(233, 5, 20, 0.63);
                transform: scale(.5);
                visibility: hidden;
                border-radius: 5px;
                transition: .5s;
                .col {
                    display: flex;
                    flex-direction: column;
                    justify-content: space-between;
                    
                    width: 50%;
                    &:first-child {
                        align-items: flex-start;
                        padding-left: 15px;
                        text-align: left;
                        @include medium;
                    }
                    &:last-child {
                        align-items: flex-end;
                        padding-right: 15px;
                        text-align: right;
                        @include black;
                    }
                    span {
                        font-size: 11px;
                        color: #fff;
                    }
                }
            }
        }
        &__img {
            position: relative;
            img {
                max-width: 100%;
                transition: 1s;
            }
        }
        &__info {
            margin-top: 15px;
            padding: 0 10px;
        }
        &__title {
            display: block;
            @include black;
            font-size: 20px;
            text-align: center;
            text-transform: uppercase;
        }
        &__difference {
            display: flex;
            // align-items: center;
            margin-top: 15px;
        }
        &__price {
            padding: 0 10px;
            width: calc(100% / 3);
            border-right: 2px solid #ef0b1a;
            &:last-child {
                border: none;
            }
            .price, .txt {
                display: block;
                @include black;
                text-transform: uppercase;
                text-align: center;
            }
            .price {
                margin-bottom: 20px;
                font-size: 18px;
            }
            .txt {
                font-size: 10px;
                color: rgba(0, 0, 0, 0.3);
            }
            img {
                margin-top: 7px;
            }
        }
        &__btn-link {
            margin: 20px auto 10px;
            padding: 15px 0;
            max-width: 270px;
            @include bold;
            letter-spacing: 0.36px;
            line-height: 25.99px;
        }
    }
    .pagination-list li {
        background-color: #f6f6f6;
    }
    .pagination-list .active {
        background-color: #ef0b1a;
    }
}

@media (max-width: 1450px) {
    .shortlist-wrap {
        justify-content: center;
    }
}
@media (max-width: 1024px) {
    .shortlist-item {
        width: calc(100% / 2 - 20px);
    }
}
@media (max-width: 620px) {
    .shortlist-item {
        max-width: 380px;
        width: 100%;
    }
}

@media (max-width: 530px) {
    .delivered-title h1 {
        font-size: 28px;
    }
    .shortlist-item {
        max-width: 100%;
    }
    .shortlist-item__img img {
        width: 100%;
    }
    .shortlist-item__info {
        padding: 0;
    }
}

@media (max-width: 420px) {
    .shortlist-item__difference {
        flex-direction: column;
    }
    .shortlist-item__price {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 20px;
        padding: 0;
        position: relative;
        width: 100%;
        border: none;
        &:before {
            content: '';
            position: absolute;
            bottom: -10px;
            width: 70px;
            height: 2px;
            background-color: #ef0b1a;
        }
        .price {
            margin-bottom: 5px;
        }
    }
    .shortlist-item__btn-link {
        margin: 20px auto;
    }
}

@media (max-width: 420px) {
    .shortlist-item__btn-link {
        max-width: 240px;
    }
}