.popul-news {
    &-wrap {
        padding: 40px 30px;
    }
    &__title {
        @include bold;
        font-size: 24px;
    }
    .list {
        margin-top: 40px;
        .item {
            margin-bottom: 30px;
            &:last-child {
                margin-bottom: 0;
            }
            a {
                display: flex;
                align-items: center;
                img {
                    margin-right: 15px;
                }
            }
        }
    }
}

.page-catalog .popul-news {
    box-shadow: 0 4px 24px rgba(0, 0, 0, 0.08);
    background-color: #ffffff;
}

@media (max-width: 1360px) {
    .page-catalog .popul-news {
        display: none;
    }
}

@media (max-width: 950px) {
    .page-news .popul-news {
        width: 100%;
    }
    .page-news .popul-news-wrap {
        padding: 25px;
    }
}