.news {
    margin-bottom: 50px;
    h4 {
        @include bold;
        font-size: 20px;
        line-height: 34.66px;
        letter-spacing: 0.24px;
        text-transform: uppercase;
    }
    hr {
        margin: 10px 0;
        width: 100%;
        height: 3px;
        background-color: #f02330;
    }
    .news-thumb {
        img {
            width: 100%;
        }
    }
    .news-content {
        letter-spacing: 0.18px;
        line-height: 23.51px;
    }
}
@media (max-width: 1024px) {
    .news-title {margin-top: 20px;}
}