.advantages {
    padding: 40px 0;
    &__title {
        margin-bottom: 25px;
        text-align: center;
    }
    &-list {
        display: flex;
        flex-wrap: wrap;
        align-content: center;
        align-items: center;
        justify-content: space-between;
        height: 650px;
        background: #fff url('../img/bg-advantages.png') no-repeat center / contain;
        &__item {
            display: flex;
            align-items: center;
            margin-bottom: 70px;
            width: calc(100% / 2 - 120px);
            img {width: 70px;}
            &:nth-child(1), &:nth-child(3), &:nth-child(5) {
                flex-direction: row-reverse;
                text-align: right;
                margin-right: 35px;
                img {
                    margin-left: 35px;
                }
            }
            &:nth-child(2), &:nth-child(4), &:nth-child(6) {
                img {
                    margin-right: 35px;
                }
            }
            span {
                font-size: 18px;
                line-height: 34.66px;
            }
        }
    }
}

@media (max-width: 1250px) {
    .advantages-list {
        margin-top: 50px;
        // background: #fff;
        height: auto;
    }
    .advantages-block {
        margin-right: 0;
        &:first-child {
            justify-content: normal;
            .advantages-list__item {
                margin-top: 50px;
                margin-bottom: 0;
            }
        }
        &:last-child {
            justify-content: normal;
            .advantages-list__item {
                margin-bottom: 50px;
            }
        }
    }
}

@media (max-width: 1024px) {
    .advantages-list {
        margin-left: auto;
        margin-right: auto;
        max-width: 800px;
    }
    .advantages-list__item {
        width: 100%;
        margin-bottom: 30px;
        img {width: 50px;}
        &:nth-child(1), &:nth-child(3), &:nth-child(5) {
            flex-direction: row;
            margin-right: 0;
            text-align: left;
            img {
                margin-left: 0;
                margin-right: 30px;
            }
        }
        &:last-child {
            margin-bottom: 0;
        }
    }
}
@media (max-width: 480px) {
    .advantages-list__item span {
        font-size: 18px;
        line-height: 1.5;
    }
}
@media (max-width: 480px) {
    .advantages-list__item {
        margin-bottom: 30px;
        &:nth-child(1), &:nth-child(2), &:nth-child(3), &:nth-child(4), &:nth-child(5), &:nth-child(6) {
            flex-direction: column;
            text-align: center;
            img {
                margin-right: 0;
                margin-bottom: 20px;
            }
        }
    }
}

@media (max-width: 380px) {
    .advantages {padding: 30px 0;}
    .advantages__title {
        font-size: 28px;
    }
}