.header {
    position: relative;
    width: 100%;
    z-index: 99;
    &-wrap {
        display: flex;
        flex-direction: column;
    }
    &-top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 100px;
        background-color: inherit;
        &__contacts {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            width: 100%;
        }
        &__tel, &__mail, &__local {
            display: flex;
            align-items: center;
            margin-right: 10px;
            font-size: 16px;
            img {
                margin-right: 6px;
            }
        }
        &__local {
            display: none;
        }
        &__btn {
            padding: 13px 23px;
            @include bold;
            font-size: 11px;
            text-align: center;
            letter-spacing: 0.82px;
            text-transform: uppercase;
            border: 3px solid #ef0b1a;
            border-radius: 100px;
            background-color: transparent;
            color: #ef0f1e;
            cursor: pointer;
            i {
                font-size: 0;
                visibility: hidden;
            }
        }
    }

    .navbar {
        position: relative;
        &:before {
            content: '';
            position: absolute;
            left: -46%;
            width: 120%;
            height: 62px;
            background-color: rgb(0,77,131);
            transform: skewX(-30deg);
            z-index: -1;
        }
        &-wrap {
            display: flex;
            align-items: center;
            justify-content: space-between;
            position: relative;
            height: 62px;
        }
        &__menu {
            @include bold;
            font-size: 13px;
            letter-spacing: 1.10px;
            text-transform: uppercase;
            transition: all .35s;
            &-link {
                margin-right: 25px;
                color: #fff;
                &:last-child {
                    margin-right: 0;
                }
            }
            .closebtn {
                display: none;
                margin-left: 40px;
                position: absolute;
                top: 23px;
                right: 37px;
                font-size: 50px;
                color: #ef0b1a;
            }
        }
        &__consultation-link {
            display: flex;
            align-items: center;
            @include medium;
            font-size: 15px;
            color: #004d83;
            svg {
                margin-right: 8px;
            }
        }
        // Дроп-меню
        label, input {
            display: none;
        }
        label {
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
        &-burger {
            &__line {
                display: block;
                margin-bottom: 10px;
                position: relative;
                width: 50px;
                height: 3px;
                background-color: #fff;
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
        .three {
            position: absolute;
        }
    }
}

@media (max-width: 1366px) {
    .header .navbar__menu-link {
        margin-right: 10px;
        font-size: 12px;
    }
    .header .navbar__consultation-link {
        font-size: 13px;
    }
}
@media (max-width: 1024px) {
    .header-top__tel, .header-top__mail {
        display: none;
    }
}
@media (max-width: 1024px) {
    .header {
        background-color: #F1EEF5;
        box-shadow: 0px 3px 10px 0px rgba(0,0,0,0.75);
        &-wrap {
            flex-direction: row;
            justify-content: space-between;
        }
        &-top {
            width: 100%;
        }
        .navbar {
            &:before {
                display: none;
            }
            &-wrap {
                height: 100px;
            }
            &__consultation-link {
                display: none;
            }
            label {
                display: flex;
                position: relative;
                right: -15px;
                width: 100px;
                height: 100%;
                background-color: #ef0f1e;
            }
            &-budger {
                width: 50px;
                height: 3px;
            }
        }
    }
    .header-top__local {
        display: flex;
        margin-right: 30px;
        font-size: 40px;
        color: #ef0f1e;
    }
    .header-top__btn {
        padding: 0;
        border: none;
        border-radius: 0;
        font-size: 0;
        i {
            font-size: 40px;
            visibility: visible;
        }
        // width: 20px;
        // height: 20px;
        // background: url('../img/icon--phone-red.png') no-repeat;
    }
    .header .navbar__menu .closebtn {
        display: block;
    }
    .header .navbar__menu {
        padding-top: 100px; 
        position: fixed; 
        z-index: 1; 
        top: 0; 
        right: 0;
        height: 100vh;
        width: 0; 
        background-color: #111; 
        overflow-x: hidden; 
        transition: 0.5s; 
        &-link {
            display: block;
            margin-right: 20px;
            padding: 8px 8px 8px 32px;
            text-align: right;
            color: #818181;
            transition: 0.3s;
            &:hover {
                color: #f1f1f1;
            }
            &:last-child {
                margin-right: 20px;
            }
        }
    }
}
@media (max-width: 540px) {
    .header-top__logo img {
        width: 200px;
    }
    .header-top, .header .navbar-wrap {
        height: 70px;
    }
    .header .navbar label {
        right: -15px;
        width: 70px;
    }
    .header .navbar-burger__line {
        margin-bottom: 7px;
        width: 30px;
    }
    .header-top__contacts i {
        font-size: 30px;
    }
}
@media screen and (max-height: 450px) {
    .navbar__menu {padding-top: 15px;}
    .navbar__menu-link {font-size: 18px;}
}

@media (max-width: 440px) {
    .header-top__logo img {
        width: 150px;
    }
}

@media (max-width: 380px) {
    .header-top__local {margin-right: 15px;}
    .header-top__btn {letter-spacing: normal; }
    .home-slider__title {font-size: 28px;text-align: center;}
}