// Пагинация
.pagination {
    margin-top: 50px;
    margin-bottom: 70px;
    &-list {
        display: flex;
        align-items: center;
        justify-content: center;
        li {
            color: #555454;
            @include medium;
            font-size: 15px;
            letter-spacing: 0.3px;
            list-style: none;
            background-color: #ffffff;
            a {
                text-decoration: none;
            }
        }
        &__prev, &__next, &__link {
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: #ffffff;
        }
        &__prev, &__next {
            width: 115px;
            height: 41px;
            border-radius: 26px;
        }
        &__prev {
            margin-right: 7px;
        }
        &__next {
            margin-left: 7px;
        }
        &__link {
            margin: 0 8px;
            width: 44px;
            height: 44px;
            border-radius: 50%;
            a {
                color: #555454;
            }
        }
    }
    .active {
        background-color: #ef0b1a;
        a {
            color: #ffffff;
        }
    }
}

@media (max-width: 540px) {
    .pagination-list__link {
        margin: 0 4px;
    }
}
@media (max-width: 460px) {
    .pagination-list {
        flex-wrap: wrap;
        justify-content: space-between;
    }
    .pagination-list__prev, .pagination-list__next {
        margin: 0;
        width: 100%;
    }
    .pagination-list__prev {
        margin-bottom: 20px;
    }
    .pagination-list__next {
        margin-top: 20px;
    }
}