.car-list {
    .car-item {
        display: flex;
        margin-bottom: 15px;
        padding: 15px;
        box-shadow: 0 4px 24px rgba(0, 0, 0, 0.08);
        background-color: #ffffff;
        &__pic {
            width: 340px;
            margin-right: 15px;
            &:hover {
                opacity: .7;
            }
            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        &__detail {
            // margin-right: 45px;
            position: relative;
            width: 100%;
        }
        header {
            display: flex;
            justify-content: space-between;
            a {
                flex: 1;
                &:hover {
                    opacity: .7;
                }
            }
        }
        &__title {
            margin: 5px 0;
            padding-bottom: 10px;
            position: relative;
            @include bold;
            font-size: 20px;
            line-height: 1.15;
            text-transform: uppercase;
            &:before {
                content: '';
                position: absolute;
                left: 0;
                bottom: 0;
                width: 53px;
                height: 2px;
                background-color: #ef0b1a;
            }
        }
        &__price {
            width: 90px;
            @include bold;
            font-size: 22px;
            line-height: 32.94px;
            text-transform: uppercase;
        }
        &__facilities-list {
            display: flex;
            flex-direction: column;
            flex-wrap: wrap;
            margin-top: 20px;
            max-height: 160px;
            li {
                display: flex;
                align-items: center;
                line-height: 37.5px;
                img {
                    margin-right: 10px;
                }
                span {
                    margin-right: 5px;
                    @include bold
                }
            }
        }
        &__btn-link {
            padding: 15px 55px;
            position: absolute;
            bottom: 10px;
            right: 0;
        }
    }
}

@media (max-width:790px) {
    .car-wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    .car-list .car-item {
        flex-direction: column;
        margin: 10px;
        width: calc(100% / 2 - 20px);
    }
    .car-list .car-item__pic {
        margin-right: 0;
        width: 100%;
    }
    .car-list .car-item__detail {
        margin-right: 0;
    }
    .car-list .car-item__price {
        font-size: 20px;
    }
    .car-list .car-item__facilities-list {
        margin-bottom: 20px;
        max-height: 100%;
    }
    .car-list .car-item__btn-link {
        padding: 15px 0;
        position: static;
    }
}
@media (max-width: 630px) {
    .car-list .car-item {
        flex-direction: column;
        margin: 10px 0;
        max-width: 380px;
        width: 100%;
    }
}

@media (max-width: 390px) {
    .car-list .car-item, .car-list .car-item__pic img {
        max-width: 100%;
    }
    .car-list .car-item {
        padding: 10px;
    }
    .car-list .car-item__title {
        font-size: 20px;
    }
    .car-list .car-item__price {
        font-size: 22px;
    }
}