.offer {
    &-wrap {
        padding: 30px;
    }
    &__title {
        margin-bottom: 25px;
        @include bold;
        font-size: 22px;
        letter-spacing: 0.23px;
        text-transform: uppercase;
        text-align: center;
    }
    &__input {
        margin-bottom: 18px;
        padding: 15px 25px;
        width: 100%;
        border: 1px solid #e4e4e4;
        border-radius: 5px;
        background-color: #ffffff;
        color: #a8a8a8;
        &:hover {
            color: #000;
        }
    }
    &__btn {
        padding: 10px 0;
        width: 100%;
        @include bold;
        font-size: 18px;
        letter-spacing: 1.56px;
        line-height: 26.66px;
        border: none;
        cursor: pointer;
    }
}

@media (max-width: 1240px) {
    .offer {
        width: 48%;
    }
}

@media (max-width: 662px) {
    .offer {
        width: 100%;
    }
}

@media (max-width: 425px) {
    .offer-wrap {
        padding: 25px;
    }
}