.fast-purchase {
    padding: 40px 0;
    &-wrap {}
    &__title {
        margin: 0 auto;
        margin-bottom: 25px;
        max-width: 740px;
        text-align: center;
        span {
            color: #ef0b1a;
        }
    }
    &-blocks {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 50px;
    }
    &-block {
        &--left {
            padding: 30px 60px 40px;
            position: relative;
            left: 100px;
            max-width: 760px;
            width: 100%;
            box-shadow: 0 4px 28px rgba(0, 0, 0, 0.08);
            background-color: #ffffff;
            z-index: 2;
            p {
                font-size: 16px;
                letter-spacing: 0.4px;
                line-height: 23.1px;
            }
        }
        &--right {
            position: relative;
            z-index: 1;
            img {
                width: 600px;
                height: 430px;
            }
        }
    }
    &__btn-link {
        margin-top: 30px;
        padding-top: 15px;
        padding-bottom: 15px;
        max-width: 270px;
        font-weight: 700;
    }
}

@media (max-width: 1366px) {
    .fast-purchase {
        &-blocks {
            flex-direction: column;
        }
        &-block {
            &--left {
                margin-bottom: 50px;
                left: 0;
            }
        }
    }
}

@media (max-width: 1200px) {
    .fast-purchase-block--left {
        margin-left: 20px;
        margin-right: 20px;
        width: auto;
    }
} 

@media (max-width: 900px) {
    .fast-purchase-block--right img {
        width: 100%;
    }
}

@media (max-width: 768px) {
    .fast-purchase-block--left {
        padding: 0 30px 30px;
    }
    .fast-purchase-block--right {
        padding-left: 15px;
        padding-right: 15px;
        img {
            width: 100%;
            height: 400px;
            object-fit: cover;
        }
    }
}
@media (max-width: 480px) {
    .fast-purchase {
        padding-top: 0;
        padding-bottom: 0;
    }
    .fast-purchase-block--right {
        img {
            width: 100%;
            height: 300px;
        }
    }
}
@media (max-width: 425px) {
    .fast-purchase-block__txt {
        font-size: 18px;
        line-height: 26px;
    }
}
