.consultation {
    &-wrap {
        padding: 30px;
    }
    &__title {

        @include bold;
        font-size: 22px;
        line-height: 38.4px;
        text-transform: uppercase;
        text-align: center;
    }
    hr {
        margin: 30px auto;
        width: 85px;
        height: 3px;
        background-color: #f02330;
    }
    &__txt {
        display: block;
        margin-bottom: 50px;
        font-size: 18px;
        line-height: 26.33px;
        text-transform: uppercase;
        text-align: center;
    }
    &__link {
        display: flex;
        align-items: center;
        justify-content: center;
        img {
            margin-right: 20px;
        }
        span {
            @include medium;
            font-size: 24px;
            letter-spacing: 0.96px;
        }
    }
}

@media (max-width: 1240px) {
    .consultation {
        width: 48%;
    }
    .page-news .consultation {
        width: 100%;
    }
    .consultation-wrap {
        padding: 30px;
    }
}

@media (max-width: 662px) {
    .consultation {
        width: 100%;
    }
    .consultation-wrap {
        padding: 20px;
    }
}