.stages {
    padding: 40px 0;
    position: relative;
    background: url('../img/bg_2.png') no-repeat;
    background-size: 100%;
    background-position-y: -100px;
    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        right: 0;
        width: 1753px;
        height: 534px;
        background: url('../img/bg-line.png');
        z-index: -1;
    }
    &-wrap {
        margin-top: 40px;
    }
    &__title {
        margin-bottom: 25px;
        text-align: center;
    }
    &-list {
        display: flex;
        flex-wrap: wrap;
        align-items: baseline;
        justify-content: center;
        padding-bottom: 50px;
        &__item {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin: 30px 0;
            padding: 0 20px;
            width: calc(100% / 3);
            // width: 100%;
            text-align: center;
            img {width: 50px;}
        }
        &__title {
            margin: 20px 0 0;
            @include black;
            font-size: 18px;
        }
        &__description {
            margin-bottom: 0;
            line-height: 23.1px;
        }
    }
}
@media (max-width: 720px) {
    .stages-list__item {
        width: calc(100% / 2);
    }
}
@media (max-width: 560px) {
    .stages {
        padding: 0;
        background-size: cover;
        &-list {
            flex-direction: column;
            // justify-content: center;
            align-items: center;
            &__item {
                margin: 15px 0;
                width: 100%;
            }
        }
    }
}

@media (max-width: 480px) {
    .stages {padding: 20px 0;}
    .stages-list__item {
        margin: 10px 0;
    }
    .stages-wrap {margin-top: 0;}
}

@media (max-width: 430px) {
    .stages-list__item {
        padding: 0;
        &:first-child {
            margin-top: 40px;
        }
    }
    .stages__title {
        // font-size: 24px;
    }
}