.btn {
    display: block;
    border: none;
    border-radius: 100px;
    box-shadow: 0 4px 22px rgba(239, 15, 30, 0.33);
    background-color: #ef0f1e;
    color: #fff;
    text-align: center;
}
.disabled {
    display: none;
}
.line--red {
    display: block;
    margin: 0 auto;
    width: 120px;
    height: 6px;
    border-radius: 50px;
    background-color: #ef0b1a;
}

.row {
    display: flex;
    justify-content: space-between;
    &-reverse {
        flex-direction: row-reverse;
    }
}

.col {
    display: flex;
    align-items: center;
    justify-content: space-between;
    &-filter {
        justify-content: flex-start;
        margin-bottom: 20px;
    }
    &-2 {
        width: 100%;
    }
}

.page-catalog {
    margin-top: 100px;
    background-color: #f6f6f6;
    .wrap {
        position: relative;
        top: -89px;
    }
}

.sidebar {
    &-catalog {
        max-width: 380px;
        width: 100%;
    }
    &-card, &-news {
        max-width: 450px;
        width: 100%;
    }
}
.content {
    max-width: 100%;
    width: 100%;
    &-news{
        margin-left: 0;
        margin-right: 20px;
        max-width: 950px;
    }
    &--no-left {
        margin-left: 0;
    }
    .block {
        margin-bottom: 25px;
        border: 1px solid #e4e4e4;
        border-radius: 5px;
        background-color: #ffffff;
        &-content {
            margin-right: 30px;
            margin-bottom: 20px;
            max-width: 65%;
        }
    }
}
.page-news {
    .block {
        margin-bottom: 25px;
        border: 1px solid #e4e4e4;
        border-radius: 5px;
        background-color: #ffffff;
        &-content {
            margin-right: 30px;
            max-width: 65%;
        }
    }
}
.page-catalog {
    .block {
        &-aside {
            margin-right: 30px;
            width: 28%;
        }
        &-list {
            width: 70%;
        }
    }
}
.page-card {
    .block-aside {width: 35%;}
}
@media (max-width: 1360px) {
    .page-catalog .row {
        flex-direction: column;
    }
    .page-catalog {
        .block {
            &-aside, &-list {
                margin-right: 0;
                width: 100%;
            }
        }
    }
}


@media (max-width: 1270px) {
    .content-card {
        margin-right: 0;
        max-width: 100%;
    }
}

@media (max-width: 1240px) {
    .row {
        &-car {
            flex-wrap: wrap;
            justify-content: center;
        }
    }
    .content .block {
        &-content {
            margin-right: 0;
            max-width: 100%;
        }
        &-aside {
            display: flex;
            justify-content: space-around;
            width: 100%;
        }
    }
}
@media (max-width: 950px) {
    .row-news {
        flex-direction: column;
    }
    .page-news .block-content {
        margin-right: 0;
        max-width: 100%;
    }
    .page-news .block-aside {
        display: flex;
        flex-wrap: wrap;
    }
}

@media (max-width: 768px) {
    .content .block-content {
        margin-bottom: 70px;
    }
}

@media (max-width: 662px) {
    .content .block-aside {
        flex-direction: column;
    }
}

@media (max-width: 610px) {
    .page-news .block-aside {
        flex-direction: column;
    }
    .page-news .sidebar-calculate {
        margin-right: 0;
    }
}
/* Модальное окно */
.modal {
    &-box {
        position: relative;
        max-width: 350px;
        border-radius: 5px;
        background-color: #fefefe;
        &-area {
            margin: 10% auto;
        }
    }
    &__title {
        margin-bottom: 25px;
        @include bold;
        font-size: 23px;
        letter-spacing: 0.23px;
        text-align: center;
    }
    &__input, &__textarea {
        margin-bottom: 20px;
        padding: 15px 25px;
        width: 100%;
        border: 1px solid #e4e4e4;
        border-radius: 5px;
        background-color: #ffffff;
        color: #a8a8a8;
        &:hover {
            color: #000;
        }
    }
    &__textarea {
        resize: vertical; 
        height: 100%;
    }

    &__files {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;
        label {
            display: block;
            margin: 10px 0;
            input {
                margin-top: 10px;
            }
            input[type=text] {
                padding: 15px 25px;
                width: 100%;
                border: 1px solid #e4e4e4;
                border-radius: 5px;
                background-color: #ffffff;
                color: #a8a8a8;
                &:hover {
                    color: #000;
                }
            }
        }
    }

    &__btn {
        padding: 15px 0;
        width: 100%;
        @include bold;
        font-size: 18px;
        letter-spacing: 1.56px;
        border: none;
        cursor: pointer;
    }
}
// Стрелки для слайдера
.arrow-slider {
    // position: absolute;
    .prev, .next {
        position: absolute;
        top: -64px;
        width: 43px;
        height: 24px;
        font-size: 0;
        border: none;
        cursor: pointer;
        opacity: .5;
        &:active {
            opacity: 1;
        }
    }
    .prev {
        left: 0;
        background: url('../img/icon--arrow-prev.png');
    }
    .next {
        right: 0;
        background: url('../img/icon--arrow-next.png');
    }
}
// Дотс для слайдера
.dots-slider {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    .dot {
        margin-right: 18px;
        height: 15px;
        border: 2px solid #b5b5b5;
        border-radius: 50%;
        background-color: rgba(68, 68, 68, 0);
        cursor: pointer;
    }
    .active {
        border: 2px solid #444444;
        background-color: #444444;
    }
}


@media (min-width: 1241px) {
    .block-aside {
        width: 30%;
        height: max-content;
        overflow: auto;
        position: sticky;
        top: 5%;
    }
}
