*, *:before, *:after {
    box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, ul, article {
    margin: 0;
    padding: 0;
}

ul {
    list-style: none;
}

p {
    margin: 18px 0;
}
@media (max-width: 750px) {
    p {
        // font-size: 16px;
        line-height: 1.5;
    }
}

input, textarea, select, button {
    outline: none;
}

a {
    text-decoration: none;
    color: #000;
}

h4 {
    @include black;
    font-size: 28px;
    color: #1a1b1d;
}
@media (max-width: 750px) {
    h4 {
        font-size: 26px;
    }
}
@media (max-width: 480px) {
    h4 {
        padding: 0 10px;
        font-size: 24px;
    }
}
@media (max-width: 380px) {
    h4 {font-size: 20px;}
}

body {
    @include regular;
    font-size: 16px;
    background: url('../img/bg_1.png') no-repeat top;
    line-height: 1.12;
}

section {
    overflow-x: hidden;
}

.container {
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}

// @media (min-width: 320px) {.container {width: 100%;}}
@media (min-width: 768px) {.container {width: 100%;}}
@media (min-width: 1024px) {.container {width: 970px;}}
@media (min-width: 1240px) {.container {width: 1000px;}}
@media (min-width: 1366px) {.container {width: 1100px;}}
@media (min-width: 1500px) {.container {width: 1200px;}}