.home-slider {
    position: relative;
    &-wrap {
        display: flex;
        justify-content: flex-end;
        position: relative;
        // height: 100%;
    }
    &-txt {
        width: 40%;
    }
    &-list {
        width: 60%;
        &:after {
            content: '';
            position: absolute;
            top: -20px;
            right: 150px;
            width: 350px;
            height: 100%;
            background-color: rgba(239, 11, 26, 0.9);
            transform: skew(-30deg);
            z-index: -1;
        }
    }
    &-item {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        height: 500px;
        img {
            display: block;
            max-width: 800px;
            position: absolute;
            top: 20%;
            right: -200px;
        }
        .price {
            display: flex;
            flex-direction: column;
            justify-content: center;
            height: 100%;
            position: absolute;
            top: 0;
            &__item {
                display: flex;
                flex-direction: column;
                margin-bottom: 40px;
                padding: 8px;
                padding-left: 50px;
                position: relative;
                width: 240px;
                background-color: #fff;
                border-radius: 5px;
                box-shadow: 0px 0px 4px 1px rgba(213,213,213,1);
                &:nth-child(1) {
                    position: relative;
                    left: 150px;
                }
                &:nth-child(2) {
                    position: relative;
                    left: 50px;
                }
                &:nth-child(3) {
                    position: relative;
                    left: -50px;
                }
                i {
                    position: absolute;
                    top: 50%;
                    left: 18px;
                    font-size: 21px;
                    transform: translateY(-50%);
                }
                .txt {
                    display: block;
                    margin-bottom: 10px;
                    font-weight: bold;
                    color: #D5D5D5;
                    text-transform: uppercase;
                }
                .num {font-size: 18px;font-weight: bold;}
            }
        }
    }
    &__title {
        margin-top: 80px;
        @include black;
        font-size: 44px;
        color: #1a1b1d;
    }
    &__description {
        max-width: 760px;
        height: 150px;
        font-size: 16px;
        line-height: 26px;
        overflow: hidden;
    }
    &__btn {
        margin-top: 40px;
        padding: 20px 0;
        width: 270px;
        @include bold;
        font-size: 16px;
        letter-spacing: 0.44px;
        text-transform: uppercase;
    }
    &__car-brand {
        position: absolute;
        bottom: 20px;
        right: 0;
        font-size: 20px;
        text-align: right;
        &:after {
            content: '';
            position: absolute;
            top: 9px;
            right: -325px;
            width: 280px;
            height: 6px;
            border-radius: 50px;
            background-color: #ef0b1a;
        }
    }
    &-dots {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        bottom: 100px;
        left: -120px;
        li {
            button {
                margin-right: 10px;
                width: 12px;
                height: 12px;
                border: none;
                border-radius: 100px;
                font-size: 0;
                background-color: #ef0f1e;
                transition: ease .5s;
            }
        }
        li.slick-active button {
            width: 43px;
        }
    }
}
@media (max-width: 1366px) {
    .home-slider-list:after {
        right: 100px;
    }
    .home-slider-item img {
        max-width: 700px;
    }
    .home-slider-dots {
        left: -80px;
    }
}

@media (max-width: 1024px) {
    .home-slider {padding-bottom: 20px;}
    .home-slider-list:after {
        right: 10%;
    }
    .home-slider__title {
        margin-top: 20px;
        text-align: center;
    }
    .home-slider__btn {
        margin-left: auto;
        margin-right: auto;
        margin-top: 30px;
    }
    .home-slider-wrap {
        flex-direction: column-reverse;
        height: 100%;
    }
    .home-slider-txt, .home-slider-list {
        width: 100%;
    }
    .home-slider-dots {
        bottom: -6px;
        left: 50%;
        transform: translateX(-50%);
    }
    .home-slider-item img {
        top: 18%;
        right: -50px;
    }
    .home-slider-item .price__item:nth-child(1) {left: 100px;}
    .home-slider-item .price__item:nth-child(2) {left: 50px;}
    .home-slider-item .price__item:nth-child(3) {left: 0;}
    .home-slider__car-brand {
        bottom: 30px;
    }
    .home-slider__description {
        margin-right: 0;
        max-width: 100%;
        height: auto;
    }
}
@media (max-width: 750px) {
    .home-slider__title {
        font-size: 32px;
    }
    .home-slider__description {
        overflow: visible;
        height: 100%;
        p {
            font-size: 18px;
        }
    }
    .home-slider__btn {
        margin: 0 auto;
    }
    
    .home-slider-item {
        height: 400px;
    }
    .home-slider-item .price {
        align-items: baseline;
        position: static;
    }
    .home-slider-item .price__item:nth-child(1),
    .home-slider-item .price__item:nth-child(2),
    .home-slider-item .price__item:nth-child(3) {
        left: 0;
    }
    .home-slider-item img {
        right: -150px;
        max-width: 600px;
    }
    .home-slider-list:after {
        right: -135px;
        width: 400px;
    }
}
@media (max-width: 540px) {
    .home-slider-item .price__item {
        margin-bottom: 30px;
    }
}
@media (max-width: 480px) {
    // .home-slider-list:after {
    //     right: -90px;
    // }
    .home-slider__btn {
        font-size: 18px;
        max-width: 320px;
        width: 100%;
    }
}
@media (max-width: 420px) {
    .home-slider-item img {
        right: -220px;
    }
} 
@media (max-width: 360px) {
    .home-slider-item img {
        right: -250px;
    }
} 