.footer {
    padding: 40px 0;
    background-color: #000106;
    // &-wrap {
    //     margin: 0 20px;
    // }
    &-block {
        display: flex;
        justify-content: space-between;
        color: #ffffff;
        &:last-child {
            margin-top: 20px;
            justify-content: center;
        }
    }
    &__menu {
        margin-right: 30px;
        @include medium;
        font-size: 18px;
        line-height: 36.69px;
    }
    &__contacts {
        line-height: 40.3px;
        span {
            font-size: 21px;
        }
        address {
            padding-left: 30px;
            position: relative;
            max-width: 420px;
            font-size: 18px;
            font-style: normal;
            .icon--point {
                position: relative;
                &:before {
                    content: '';
                    position: absolute;
                    top: 3px;
                    left: -27px;
                    width: 15px;
                    height: 20px;
                    background: url('../img/icon--point.png');
                }
            }
        }
        a {
            display: block;
            padding-left: 30px;
            position: relative;
            font-size: 18px;
            color: #ffffff;
            &[href^=tel]:before {
                content: '';
                position: absolute;
                top: 14px;
                left: 0;
                width: 16px;
                height: 16px;
                background: url('../img/icon--phone-red.png');
            }
            &[href^=mailto]:before {
                content: '';
                position: absolute;
                top: 17px;
                left: 0;
                width: 19px;
                height: 12px;
                background: url('../img/icon--mail-red.png');
            }
        }

    }
    &__form {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        span {
            @include medium;
            font-size: 18px;
            letter-spacing: 0.44px;
            line-height: 39.69px;
        }
        form {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            margin-top: 15px;
            input[type=tel] {
                padding: 6px 40px;
                height: 55px;
                border: none;
                border-radius: 54px;
                font-size: 24px;
                line-height: 43.3px;
                background-color: #c4c4c4;
                color: rgba(0, 0, 0, 0.32);
                text-align: right;
            }
            .footer__btn {
                margin-top: 15px;
                padding: 10px 60px;
                border: none;
            }
        }
    }
    &__social {
        display: flex;
        align-items: center;
        margin-top: 35px;
        li {
            margin-left: 20px;
        }
    }
    &__reserved {
        @include medium;
        font-size: 18px;
        line-height: 39.69px;
        color: rgba(255, 255, 255, 0.37);
    }
}

@media (max-width: 1020px) {
    .footer__menu {
        display: none;
    }
}

@media (max-width: 760px) {
    .footer__contacts address {
        max-width: 420px;
    }
    .footer-block, .footer__contacts, .footer__form, .footer__form form {
        flex-direction: column;
        align-items: center;
    }
    .footer__contacts, .footer__reserved, .footer__form form input[type=tel], .footer__form span {
        text-align: center;
    }
    .footer__contacts {
        display: flex;
        margin-bottom: 30px;
    }
}

@media (max-width: 410px) {
    .footer__form form input[type=tel] {
        padding: 4px 20px;
        height: auto;
    }
}

@media (max-width: 370px) {
    .footer__form form input[type=tel] {
        max-width: 290px;
        padding-right: 5px;
        padding-left: 5px;
    }
}
