.specifications {
    width: 100%;
    &-wrap {
        padding: 30px;
    }
    &__title {
        @include bold;
        font-size: 24px;
        text-transform: uppercase;
        text-align: center;
    }
    &-list {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        margin-top: 40px;
        margin-bottom: 20px;
        li {
            display: flex;
            align-items: center;
            margin-bottom: 25px;
            padding-left: 50px;
            position: relative;
            // line-height: 37.5px;
            span {
                margin-right: 5px;
                @include bold
            }
        }
    }
    &-year, &-mileage, &-box, &-drive, &-fuel, &-engine, &-auction, &-сar-body, &-condition,
    &-bought, &-delivered, &-repair-cost {
        position: relative;
        &:before {
            content: '';
            position: absolute;
            left: 0;
            top: -5px;
        }
    }
    &-year:before {
        width: 25px;
        height: 28px;
        background: url('../img/icon--year.png') no-repeat;
    }
    &-mileage:before {
        width: 30px;
        height: 20px;
        background: url('../img/icon--mileage.png') no-repeat;
    }
    &-box:before {
        width: 31px;
        height: 27px;
        background: url('../img/icon--box.png') no-repeat;
    }
    &-drive:before {
        width: 30px;
        height: 20px;
        background: url('../img/icon--drive.png') no-repeat;
    }
    &-fuel:before {
        width: 22px;
        height: 27px;
        background: url('../img/icon--fuel.png') no-repeat;
    }
    &-engine:before {
        width: 31px;
        height: 27px;
        background: url('../img/icon--engine.png') no-repeat;
    }
    &-auction:before {
        width: 27px;
        height: 27px;
        background: url('../img/icon--auction.png') no-repeat;
    }
    &-сar-body:before {
        width: 30px;
        height: 25px;
        background: url('../img/icon--сar-body.png') no-repeat;
    }
    &-condition:before {
        width: 27px;
        height: 23px;
        background: url('../img/icon--condition.png') no-repeat;
    }

    &-bought:before {
        width: 16px;
        height: 28px;
        background: url('../img/icon--bought.png') no-repeat;
    }
    &-delivered:before {
        width: 28px;
        height: 28px;
        background: url('../img/icon--calendar.png') no-repeat;
    }
    &-repair-cost:before {
        width: 27px;
        height: 23px;
        background: url('../img/icon--repair-cost.png') no-repeat;
    }
    &__btn-link {
        padding: 10px 0;
        @include bold;
        font-size: 20px;
        letter-spacing: 2.16px;
        line-height: 34.66px;
    }
}

@media (max-width: 1240px) {
    .specifications-wrap {
        padding: 30px;
    }
    .specifications-list {
        flex-direction: row;
        li {
            margin-right: 30px;
        }
    }
    .specifications__btn-link {
        max-width: 320px;
    }
}

@media (max-width: 425px) {
    .specifications-wrap {
        padding: 25px;
    }
}
@media (max-width: 375px) {
    .specifications__title {
        font-size: 24px;
    }
    .specifications-list li {
        flex-wrap: wrap;
    }
} 

@media (max-width: 340px) {
    .specifications__title {
        font-size: 20px;
    }
}
