.form-search {
    margin-bottom: 30px;
    padding: 30px;
    border-top: 3px solid #ef0b1a;
    box-shadow: 0 4px 22px rgba(0, 0, 0, 0.08);
    background-color: #fff;
    &-block {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 20px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    &-inner {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        max-width: 930px;
        width: 100%;
    }
    &__title {
        @include bold;
        font-size: 22px;
    }
    .sort span, .show span, .type span {
        margin-right: 20px;
        @include medium;
        font-size: 14px;
        letter-spacing: 0.15px;
        line-height: 20.66px;
        color: rgba(0, 0, 0, 0.5);
    }
    .sort, .show {
        font-size: 15px;
        select {
            padding: 5px 20px;
            max-width: 160px;
            height: 34px;
            border: 1px solid #dddddd;
            border-radius: 5px;
            color: rgba(0, 0, 0, 0.3);
        }
    }
    .type { 
        display: flex;
        align-items: center;
        ul {
            display: flex;
            align-items: center;
            margin-left: 50px;
            li {
                margin-right: 15px;
                &:last-child {
                    margin-right: 0;
                }
            }
        }
    }

    //- Стиль формы поиска
    .form-search-1 {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        .form-search__btn {
            margin-top: 0;
            &--search {
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
            }
            &--close {
                padding: 15px 10px;
                width: 48px;
                background-color: #004d83;
                border-top-left-radius: 0;
                border-bottom-left-radius: 0;
            }
        }
        input {padding: 0 10px;}
        .form-blockbtn {
            display: flex;
            align-items: center;
        }
    }
    form {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 100%;
        input {
            padding: 15px 15px 5px;
            height: 47px;
            border: 1px solid #a6a6a6;
            @include medium;
            font-size: 16px;
            line-height: 25.99px;
            background-color: #f8f8f8;
            &:focus {
                border-color: #ef0b1a;
                background-color: #fff;
            }
        }
        .brand {
            width: calc(100% / 3 - 15px);
            border-radius: 5px;
        }

        .years, .price {
            display: flex;
            width: calc(100% / 3);
        }
        .years, .price {
            &__from, &__to, &__of, &__up {
                position: relative;
                width: 100%;
            }
            &__from, &__of {
                // border-right: none;
                border-top-left-radius: 5px;
                border-bottom-left-radius: 5px;
            }
            &__to, &__up {
                border-top-right-radius: 5px;
                border-bottom-right-radius: 5px;
            }
        }
        .form-search__btn {
            display: block;
            margin-top: 20px;
            padding: 15px 30px;
            max-width: 270px;
            width: 100%;
            position: relative;
            border: none;
            cursor: pointer;
            &:active {
                top: -1px;
            }
            .icon--search {
                margin-right: 30px;
                position: relative;
                &:before {
                    content: '';
                    position: absolute;
                    left: 0;
                    width: 20px;
                    height: 20px;
                    background: url('../img/icon--search.png');
                }
            }
        }
    }
}

@media (max-width: 1298px) {
    .form-search form .form-search__btn {
        margin-top: 20px;
    }
}

@media (max-width: 1024px) {
    .form-search-1 {
        flex-direction: column;
        span {
            text-align: center;
        }
        input.brand {
            margin: 15px 0;
            padding: 0;
            max-width: 100% !important;
            width: 100%;
        }
    }
}

@media (max-width: 840px) {
    .form-search-block .row {
        display: none;
    }
    .form-search-inner {
        display: none;
    }
}

@media (max-width: 778px) {
    .form-search form {
        flex-direction: column;
    }
    .form-search form .brand, 
    .form-search form .years, 
    .form-search form .price {
        margin-right: 0;
        max-width: 100%;
        width: 100%;
    }
    .form-search form .years {
        margin-top: 20px;
    }
}