.serve {
    padding: 40px 0;
    &-wrap {}
    &__title {
        margin-bottom: 25px;
        text-align: center;
        span {
            color: #ef0b1a;
        }
    }
    &-block {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        margin-top: 80px;
        position: relative;
        &:before {
            content: '';
            position: absolute;
            left: -191px;
            top: -78px;
            width: 550px;
            height: 413px;
            background-image: url('../img/mercedes.png');
            background-size: contain;
            z-index: 1;
        }
    }
    &__txt {
        margin-left: 50px;
        padding-left: 30px;
        position: relative;
        max-width: 750px;
        font-size: 16px;
        letter-spacing: 0.32px;
        line-height: 29.26px;
        z-index: 10;
    }
    &__btn-link {
        margin-top: 50px;
        padding: 20px 15px;
        max-width: 270px;
        font-weight: 700;
    }

    &-list {
        display: flex;
        justify-content: center;
        margin-top: 100px;
        position: relative;
        z-index: 10;
        &__item {
            display: flex;
            flex-wrap: wrap;
            flex-direction: column;
            align-items: center;
            margin-right: 80px;
            padding: 0 10px;
            position: relative;
            width: calc(100% / 3);
            // width: 100%;
            text-align: center;
            img {width: 70px;}
            &:last-child {
                margin-right: 0;
            }
            &:nth-child(1):after, &:nth-child(2):after {
                content: '';
                position: absolute;
                right: -55px;
                top: 28px;
                width: 22px;
                height: 42px;
                background: url('../img/icon--arrow-silver.png');
            }
            span {
                margin-top: 15px;
                @include bold;
                font-size: 18px;
                letter-spacing: 0.42px;
                line-height: 25.99px;
            }
            p {
                // padding-bottom: 20px;
                letter-spacing: 0.36px;
                line-height: 23.1px;
            }
        }
    }
}

@media (max-width: 1366px) {
    .serve {
        &-block {
            justify-content: center;
            margin-top: 50px;
            &:before {
                display: none;
            }
        }
        &__txt {
            margin-left: 0;
            padding-left: 0;
            p {
                text-align: center;
            }
        }
        &__btn-link {
            margin: 50px auto 0;
            
        }
    }
}

@media (max-width: 1024px) {
    .serve-list {
        flex-direction: column;
        align-items: center;
        &__item {
            margin-right: 0;
            margin-bottom: 20px;
            width: 100%;
            &:last-child {
                margin-bottom: 0;
            }
            &:nth-child(1):after, &:nth-child(2):after {
                display: none;
            }
        }
    }
} 
@media (max-width: 480px) {
    .serve-block{
        margin-top: 0;
    }
}
@media (max-width: 385px) {
    .serve__btn-link {
        width: 300px;
    }
}