.car {
    margin-bottom: 100px;
    background: transparent;
    // Галерея
    .gallery {
        margin-right: 30px;
        max-width: 65%;
        img {
            width: 100%;
        }
        &-for {
            position: relative;
            display: none;
            #imgtext {
                position: absolute;
                bottom: 15px;
                left: 15px;
                color: white;
                font-size: 20px;
            }
            .closebtn {
                position: absolute;
                top: 10px;
                right: 15px;
                color: white;
                font-size: 35px;
                cursor: pointer;
            }
        }
        &-nav {
            display: flex;
            &:after {
                content: '';
                display: table;
                clear: both;
            }
            &__slide {
                padding: 10px;
                width: 20%;
                img {
                    width: 100%;
                    opacity: 0.8;
                    cursor: pointer;
                    &:hover {
                        opacity: 1;
                    }
                }
            }
        }
    }




    &-info, &-condition, &-form, &-review, &-stage {
        margin-bottom: 40px;
        &__title {
            padding-left: 48px;
            padding-bottom: 15px;
            position: relative;
            border-bottom: 3px solid #f02330;
            @include bold;
            font-size: 22px;
            text-transform: uppercase;
            &:before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
            }
        }
    }
    &-info, &-condition, &-stage {
        &__txt {
            letter-spacing: 0.18px;
            line-height: 28.51px;
        }
    }
    &-stage {
        .remark {
            font-size: 11px;
            line-height: 24px;
            letter-spacing: 0.16px;
            span {
                padding-right: 10px;
                font-size: 22px;
                font-weight: 700;
                // line-height: 31.77px;
                text-transform: uppercase;
            }
        }
    }
    &-stage__list {
        margin-top: 20px;
        li {
            display: flex;
            align-items: baseline;
            padding: 10px 15px 10px 0;
            .img {
                display: flex;
                align-items: center;
                justify-content: center;
                margin-right: 10px;
                width: 40px;
            }
            .txt {width: 100%;line-height: 24px;}
            strong {margin-right: 10px;}
            &:nth-child(even) {background-color: #f4f4f4;}
            &:nth-child(odd) {background-color: #fff;}
        }
    }
    
    &-info__title:before, &-stage__title:before {

        width: 31px;
        height: 31px;
        background: url('../img/icon--info.png');
    }
    &-condition__title:before, &-stage__title-two:before {
        width: 29px;
        height: 28px;
        background: url('../img/icon--condition--red.png');
    }
    &-stage__title-three:before {
        position: absolute;
        top: -6px;
        left: 0;
        width: 30px;
        height: 30px;
        background: url('../img/icon--auc.png') no-repeat;
    }
    &-form__title:before {
        width: 28px;
        height: 23px;
        background: url('../img/icon--plane--red.png');
    }
    &-review__title:before {
        width: 35px;
        height: 30px;
        background: url('../img/icon--reviews.png');
    }
    // Блок обратной связи
    &-form {
        form {
            display: flex;
            justify-content: space-between;
            margin-top: 30px;
            position: relative;
            width: 100%;
            input, textarea {
                border: 1px solid #e4e4e4;
                border-radius: 5px;
                background-color: #ffffff;
                color: #a8a8a8;
                &:focus {
                    color: #000;
                }
            }
        }
        &__input {
            display: flex;
            flex-direction: column;
            margin-right: 20px;
            width: 50%;
            input {
                margin-bottom: 18px;
                padding: 15px 35px;
                letter-spacing: 0.18px;
                line-height: 28.51px;
            }
        }
        textarea {
            padding: 15px 25px;
            width: 50%;
        }
        &__btn {
            padding: 15px 75px;
            position: absolute;
            bottom: -40px;
            max-width: 280px;
            border: none;
            @include bold;
            line-height: 24.51px;
            text-transform: uppercase;
        }
    }
    // Блок отзывов
    &-review {
        margin-top: 30px;
        .review {
            margin-top: 30px;
            &-header {
                display: flex;
                align-items: center;
            }
            &-photo {
                margin-right: 30px;
                border-radius: 50%;
            }
            &-author {
                display: flex;
                flex-direction: column;
                &__txt {
                    @include bold;
                    font-size: 24px;
                    letter-spacing: 0.24px;
                    
                }
                &__time {
                    letter-spacing: 0.18px;
                    line-height: 28.51px;
                    color: rgba(0, 0, 0, 0.29);
                }
            }
            &-content {
                font-style: italic;
                letter-spacing: 0.18px;
                line-height: 28.51px;
                color: rgba(0, 0, 0, 0.82);
            }

        }
    }
}

@media (max-width: 1240px) {
    .car {
        margin-bottom: 30px;
    }
    .car .gallery {
        margin-right: 0;
        margin-bottom: 20px;
        max-width: 100%;
    }
    .car .fotorama__stage {
        margin: 0 auto;
    }
}

@media (max-width: 768px) {
    .car-form form {
        flex-direction: column;
        align-items: center;
    }
    .car-form__input {
        margin-right: 0;
        width: 100%;
    }
    .car-form form textarea {
        width: 100%;
    }
    .car-form__btn {
        bottom: -74px;
    }
}

@media (max-width: 425px) {
    .car-list .car-item__pic {
        width: 100%;
    }
}