.catalog {
    padding: 40px 0;
    background-color: #fafafa;
    &-wrap {}
    &__title {
        margin-bottom: 25px;
        text-align: center;
    }
    &-slider {
        margin-top: 40px;
        // margin-bottom: 60px;
        position: relative;
        .tab {
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 30px 0;
            &__links {
                display: flex;
                align-items: center;
                position: relative;
                font-size: 18px;
                color: #7c7c7c;
                cursor: pointer;
                svg {
                    margin-right: 15px;
                    width: 35px;
                }
                &--active {
                    color: #000000;
                }
            }
        }
        // Каталог, слайдер
        //-----------------//
        .slider-item {
            margin: 10px;
            .column {
                position: relative;
                overflow: hidden;
                &:hover .details {
                    transform: scale(1);
                    visibility: visible;
                }
                .details {
                    display: flex;
                    padding: 10px 0;
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    height: 100%;
                    background-color: rgba(233, 5, 20, 0.63);
                    transform: scale(.5);
                    visibility: hidden;
                    border-radius: 5px;
                    transition: .5s;
                    .col {
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                        
                        width: 50%;
                        &:first-child {
                            align-items: flex-start;
                            padding-left: 15px;
                            text-align: left;
                            @include medium;
                        }
                        &:last-child {
                            align-items: flex-end;
                            padding-right: 15px;
                            text-align: right;
                            @include black;
                        }
                        span {
                            font-size: 11px;
                            color: #fff;
                        }
                    }
                }
            }
            &__img {
                position: relative;
                img {
                    max-width: 100%;
                    transition: 1s;
                }
            }
            &__info {
                margin-top: 25px;
                padding: 0 10px;
            }
            &__title {
                display: block;
                @include black;
                font-size: 20px;
                text-align: center;
                text-transform: uppercase;
            }
            &__difference {
                display: flex;
                // align-items: center;
                margin-top: 15px;
            }
            &__price {
                padding: 0 10px;
                width: 33.33%;
                border-right: 2px solid #ef0b1a;
                &:last-child {
                    border: none;
                }
                .price, .txt {
                    display: block;
                    @include black;
                    text-transform: uppercase;
                    text-align: center;
                }
                .price {
                    margin-bottom: 10px;
                    font-size: 18px;
                }
                .txt {
                    font-size: 10px;
                    color: rgba(0, 0, 0, 0.3);
                }
                img {
                    width: auto;
                    margin-top: 7px;
                }
            }
        }
    }
    &__btn {
        margin: 0 auto;
        padding-top: 25px;
        padding-bottom: 20px;
        max-width: 300px;
        font-weight: 700;
    }
    // Tабы
    .resp-tabs-container {
        padding: 0px;
        background-color: transparent;
    }
    .resp-tab-active {
        color: #000000;
        svg {
            fill: #ef0b1a;
        }
    }
    h2.resp-accordion {
        display: none;
    }
}

@media (max-width: 1400px) {
    .catalog-slider .slider {
        justify-content: center;
    }
    .catalog-slider .slider-item:nth-child(3) {
        display: none;
    }
    .catalog-slider .dots .prev {
        left: 20px;
    }
    .catalog-slider .dots .next {
        right: 20px;
    }
}

@media (max-width: 1000px) {
    .catalog-slider .slider-item {
        margin-left: auto;
        margin-right: auto;
        max-width: 380px;
    }
    
}
@media (max-width: 950px) {
    .catalog-slider .dots {
        .prev, .next {
            top: 35%;
        }
    }
    
}

@media (max-width: 600px) {
    .catalog-slider .dots {
        display: none;
    }
}

@media (max-width: 540px) {
    .catalog-slider .tab {
        flex-direction: column;
    }
    .catalog-slider .tab__links {
        margin-right: 0;
        margin-bottom: 0px;
        &:last-child {
            margin-bottom: 0;
        }
    }
}

@media (max-width: 480px) {
    .catalog-slider .slider-item, .catalog-slider .slider-item__img img {
        max-width: 100%;
    }
}

@media (max-width: 420px) {
    .catalog-slider {
        margin-bottom: 0px;
    }
    .catalog-slider .slider-item__difference {
        flex-direction: column;
    }
    .catalog-slider .slider-item__price {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 20px;
        position: relative;
        width: 100%;
        border: none;
        &:before {
            content: '';
            position: absolute;
            bottom: -10px;
            width: 70px;
            height: 2px;
            background-color: #ef0b1a;
        }
        .price {
            margin-bottom: 5px;
        }
    }
    .catalog__btn {
        max-width: 100%;
    }
    .catalog__title {
        font-size: 24px;
    }
}
@media (max-width: 380px) {
    .catalog {
        padding: 30px 0;
    }

    .catalog-slider .tab {
        margin: 15px 0;
    }
}
@media (max-width: 360px) {
    .catalog__btn {
        padding-top: 10px;
        padding-bottom: 10px;
    }
}