.navigation {
    position: relative;
    top: -15px;
    height: 80px;
    overflow: hidden;
    z-index: 1;
    &-wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .breadcrumbs {
        &-menu {
            display: flex;
            li {
                margin-right: 50px;
                position: relative;
                font-size: 16px;
                &:before {
                    content: '';
                    position: absolute;
                    right: -23px;
                    top: 3px;
                    width: 7px;
                    height: 12px;
                    background: url('../img/icon--arrow-small.png');
                }
                &:last-child {
                    margin-right: 0;
                    &:before {
                        display: none;
                    }
                }
                .active {
                    color: rgba(0, 0, 0, 0.37);
                }
            }
        }
    }
    .option {
        display: flex;
        align-items: center;
        position: relative;
        height: 80px;
        &:before {
            content: '';
            position: absolute;
            right: -75%;
            width: 1030px;
            height: 100%;
            background-color: #f02330;
            transform: skewX(-30deg);
            z-index: -1;
        }
        &__item {
            display: flex;
            align-items: center;
            padding: 0 20px;
            color: #fff;
            border-right: 1px solid #fff;
            &:last-child {
                padding-right: 0;
                border: none;
            }
            &:first-child {
                padding-left: 0;
            }
            img {
                margin-right: 20px;
                width: 30px;
            }
            span {
                font-size: 18px;
                
            }
        }
    }
}

@media (max-width: 1024px) {
    .navigation {
        position: static;
        background-color: #f02330;
    }
    .breadcrumbs {
        display: none;
    }
    .navigation, .navigation .option {
        width: 100%;
        height: 60px;
    }
    .navigation .option {
        justify-content: center;
        &:before {
            display: none;
        }
    }
}

@media (max-width: 840px) {
    .navigation {
        display: none;
    }
} 
