.info {
    padding: 40px 0;
    &-wrap {}
    &__title {
        margin: auto;
        margin-bottom: 25px;
        max-width: 750px;
        text-align: center;
        span {
            color: #ef0b1a;
        }
    }
    .accordion {
        margin-top: 50px;
        .item {
            margin-bottom: 20px;
            padding: 30px 50px;
            width: 100%;
            border-radius: 5px;
            overflow: hidden;
            box-shadow: 0 4px 28px rgba(0, 0, 0, 0.08);
            background-color: #ffffff;
            label {
                display: flex;
                align-items: center;
                position: relative;
                width: 100%;
                cursor: pointer;
                .line {
                    position: absolute;
                    right: 0;
                    width: 30px;
                    height: 3px;
                    background-color: #ef0b1a;
                    transition: all .35s;
                }
                .two {
                    transform: rotate(90deg);
                }
            }
            .number, .title {
                @include bold;
                text-transform: uppercase;
            }
            .number {
                font-size: 20px;
                color: #ef0b1a;
            }
            .title {
                margin-top: 7px;
                padding-left: 10px;
                padding-right: 40px;
                font-size: 20px;
                color: #232323;
            }
            .content {
                margin-top: 0;
                max-height: 0;
                font-size: 0;
                line-height: 23.1px;
                // padding: 0 1em;
                background: white;
                transition: all .35s;
            }
            input {
                display: none;
            }
            input:checked {
            + label {
                .one {
                    transform: rotate(-45deg);
                }
                .two {
                    transform: rotate(45deg);
                }
                
            }
            ~ .content {
                // margin-top: 30px;
                max-height: max-content;
                font-size: 16px;
            }
            }
        }
    }
}

@media (max-width: 880px) {
    .info .accordion .item .title {
        font-size: 20px;
    }
}

@media (max-width: 748px) {
    .info .accordion .item {
        padding: 15px 20px;
    }
}

@media (max-width: 600px) {
    .info .accordion .item .number {
        font-size: 30px;
    }
    .info .accordion .item .title {
        font-size: 18px;
    }
}
@media (max-width: 480px) {
    .info .accordion .item .title {
        margin-top: 3px;
        font-size: 14px;
    }
}
@media (max-width: 420px) {
    .info .accordion .item .number {
        font-size: 20px;
    }
}

@media (max-width: 375px) {
    .info .accordion .item {
        padding: 10px 15px;
    }
}