.sidebar-calculate {
    .wrap {
        padding: 30px;
    }
    .title {
        @include bold;
        font-size: 22px;
        text-transform: uppercase;
        text-align: center;
    }
    .list {
        margin: 40px 0;
        padding-left: 10px;
        .item {
            position: relative;
            padding-left: 42px;
            @include bold;
            letter-spacing: 0.18px;
            line-height: 46.06px;
            &:last-child {
                margin-bottom: 0;
            }
            &:before {
                content: '';
                position: absolute;
                top: 12px;
                left: 0;
                width: 28px;
                height: 20px;
                background: url('../img/icon--check.png');
            }
        }
    }
    &__btn-link {
        margin: 0 auto;
        padding: 10px 15px;
        max-width: 330px;
        @include bold;
        letter-spacing: 1.56px;
    }
}
@media (max-width: 1240px) {
    .sidebar-calculate {
        width: 48%;
    }
    .page-news .sidebar-calculate {
        width: 100%;
    }
}

@media (max-width: 662px) {
    .sidebar-calculate {
        width: 100%;
    }
    .sidebar-calculate .wrap {
        padding: 30px;
    }
}

@media (max-width: 425px) {
    .sidebar-calculate .wrap {
        padding: 25px;
    }
}
@media (max-width: 375px) {
    .sidebar-calculate .title {
        font-size: 24px;
    }
} 

@media (max-width: 340px) {
    .sidebar-calculate .title {
        font-size: 20px;
    }
}