.legal {
    padding: 40px 0;
    &-wrap {}
    &__title {
        margin-bottom: 22px;
        text-align: center;
    }
    &-block {
        display: flex;
        margin: auto;
        margin-top: 30px;
        width: 100%;
        height: max-content;
        border-radius: 5px;
        box-shadow: 0 4px 25px rgba(0, 0, 0, 0.07);
        background-color: #ffffff;
        &--left {
            position: relative;
            // width: 300px;
            background: url('../img/img-1.png');
            &:after {
                content: '';
                position: absolute;
                bottom: 32px;
                right: 0;
                width: 290px;
                height: 343px;
                background: url('../img/camry.png');
            }
            span {
                display: block;
                padding: 65px 50px 0 70px;
                position: relative;
                @include medium;
                font-size: 28px;
                line-height: 40px;
                color: #ffffff;
                &:after {
                    content: '';
                    position: absolute;
                    bottom: -27px;
                    left: 70px;
                    width: 80px;
                    height: 3px;
                    border-radius: 50px;
                    background-color: #ffffff;
                }
            }
        }
        &--right {
            display: flex;
            .tab-bar {
                display: flex;
                flex-direction: column;
                margin: 0;
                &__item {    
                    display: flex;
                    align-items: center;   
                    padding: 0 30px;
                    position: relative;
                    width: 300px;
                    height: 100%;
                    border: none;
                    border-right: 2px solid #f2f2f2;
                    border-bottom: 2px solid #f2f2f2;
                    background-color: transparent;
                    @include bold;
                    line-height: 22.81px;
                    font-size: 16px;
                    text-align: left;
                    outline: none;
                    cursor: pointer;
                    &:nth-child(1):after { content: '01'; }
                    &:nth-child(2):after { content: '02'; }
                    &:nth-child(3):after { content: '03'; }
                    &:nth-child(4):after { content: '04'; }
                    &:after {
                        position: absolute;
                        right: 25px;
                        top: 70px;
                        @include bold;
                        font-size: 52px;
                        color: rgba(0, 0, 0, 0.04);
                    }
                    &:last-child {
                        border-bottom: none;
                    }
                }
                &__item {
                    margin: 0;
                }
            }
            .content {
                margin: 30px 0;
                // width: 710px;
                height: 580px;
                overflow-y: auto;
                &__txt {
                    padding: 0 40px;
                    width: 100%;
                    span {
                        @include bold;
                        font-size: 24px;
                        color: #ef0b1a;
                        line-height: 36.5px;
                    }
                    p {
                        margin: 15px 0;
                        width: 100%;
                        @include medium;
                        font-size: 16px;
                        line-height: 28px;
                    }
                }
            }
        }
    }
    // Таб
    #legalTabs {
        display: flex !important;
    }
    .resp-tab-active {
        border-right: 2px solid #ef0f1e;
        color: #ef0f1e;
    }

}

@media (max-width: 1366px) {
    .legal-block {
        width: 100%;
        &--left {display: none;}
        &--right {
            .content {
                width: 100%;
                height: 400px;
            }
        }
    }
}
@media only screen and (max-width: 768px) {
    .legal {
        &-block {
            height: auto;
            &--right {
                .content {height: auto;}
                .content__txt {
                    padding: 0 15px;
                    span {
                        display: none;
                    }
                    p {
                        font-size: 16px;
                        line-height: 26px;
                    }
                }
            }
        }
        
        ul.resp-tabs-list {
            display: none;
        }
    
        h2.resp-accordion {
            display: block;
            font-size: 18px;
        }
    
        .resp-vtabs .resp-tab-content {
            border: none;
        }
    
        .resp-vtabs .resp-tabs-container {
            border: none;
            float: none;
            width: 100%;
            min-height: 100px;
            clear: none;
        }
    
        .resp-accordion-closed {
            display: none !important;
        }
    
        .resp-vtabs .resp-tab-content:last-child {
            border-bottom: 1px solid #c1c1c1 !important;
        }
        h2.resp-accordion {
            border: none;
        }
    }
}
@media (max-width: 380px) {
    .legal {
        padding: 0;
        padding-bottom: 30px;
    }
}