.reviews {
    padding: 40px 0;
    &-wrap {}
    &__title {
        margin-bottom: 25px;
        text-align: center;
    }
    &-slider {
        margin-top: 20px;
        margin-bottom: 30px;
        position: relative;
    }
    &-item {
        margin: 30px auto;
        padding: 20px 15px;
        max-width: 700px;
        height: auto;
        box-shadow: 0 4px 28px rgba(0, 0, 0, 0.08);
        background-color: #ffffff;
        &__photo {
            border-radius: 50%;
        }
        &__info {
            margin-left: 20px;
        }
        &__user {
            display: flex;
            align-items: center;
            margin-bottom: 20px;
        }
        &__name {
            @include bold;
            font-size: 18px;
            text-transform: uppercase;
        }
        &__brand-car {
            margin-top: 5px;
            @include medium;
            font-size: 15px;
        }
        &__date{
            margin-top: 10px;
            font-style: italic;

        }
        &__message {
            display: flex;
            flex-direction: column;
            @include medium;
            font-style: italic;
            line-height: 26.13px;
            width: 100%;
            height: 340px;
            overflow: hidden;
        }
        &__txt {
            flex: 1;
            margin-bottom: 15px;
            height: 130px;
            overflow: hidden;
            p {margin: 0;}
        }
        &__img {
            display: flex;
            align-items: center;
            a {
                &.review-img {
                    margin: 0 3px;
                    width: 50%;
                    height: 200px;
                    
                    img {
                        height: 100%;
                        object-fit: cover;
                    }
                }
                &.review-video {
                    position: relative;
                    width: 50%;
                    height: 200px;
                    img {
                        max-width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                    &:after {
                        content: '';
                        width: 0;
                        height: 0;
                        border-top: 20px solid transparent;
                        border-left: 40px solid #fff;
                        border-bottom: 20px solid transparent;
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%,-50%);
                    }
                }
                &.review-video-full {
                    width: 100%;
                    height: 340px;
                }
            }
            
        }
        
    }
    &__btn-link {
        margin-top: 50px;
        margin-left: auto;
        margin-right: auto;
        padding: 20px 15px;
        max-width: 270px;
        width: 100%;
        font-weight: 700;
    }
}

@media (min-width: 481px) {
    .reviews-item__img a:not(:nth-child(-n + 2)) {
        display: none;
    }
    .reviews-item__img a {
        &.review-img {
            margin: 0 3px;
            width: 50%;
            height: 200px;
        }
    }
}
@media (max-width: 480px) {
    .reviews-slider .reviews-item {
        padding: 15px;
    }
    .reviews-item__img a:not(:first-child) {
        display: none;
    }
    .reviews-item__img a.review-img {
        width: 100%;
        height: 200px;
    }
    .reviews-item__img a.review-video {
        width: 100%;
        height: 200px;
    }
}

@media (max-width: 428px) {
    .reviews {padding: 0;}
    .reviews-slider .reviews-item__user {
        flex-direction: column;
        text-align: center;
    }
    .reviews-slider .reviews-item__info {
        margin-left: 0;
        margin-top: 10px;
    }
}