.car-header {
    margin: 0 0 20px;
    padding-bottom: 20px;
    border-bottom: 3px solid #f02330;
    &-wrap {
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
    }
    &__title {
        @include bold;
        font-size: 38px;
        text-transform: uppercase;
    }
    &__price {
        @include medium;
        font-size: 20px;
        text-transform: uppercase;
        span {
            padding-left: 10px;
            @include bold;
            font-size: 36px;
            color: #f02330;
        }
    }
}

@media (max-width: 1024px) {
    .car-header {margin-top: 20px;}
    .car-header__title, .car-header__price span {font-size: 28px;}
    .car-header__price {font-size: 16px;}
}
@media (max-width: 648px) {
    .car-header__title, .car-header__price span {font-size: 28px;}
}